<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === true }">
    <div class="popup__bg"></div>
    <div
      class="common__popup treatment__preview__popup box__shadow__02"
      :class="{ step2: popupStep === 2 }"
    >
      <button
        v-if="popupStep === 2"
        class="back__arrow box__shadow__02"
        @click="historyBack"
      ></button>
      <div class="popup__top">
        <h4>
          {{
            popupStep === 1
              ? this.treatmentInfo.name
              : `${this.treatmentInfo.name} - ${medicineInfo.name}`
          }}
        </h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <!-- 치료제 리스트 -->
      <div class="treatment__list vertical__scroll" v-if="popupStep === 1">
        <!-- 치료제 -->
        <div
          class="item box__shadow__03"
          v-for="(medicine, idx) in medicineList"
          :key="idx"
          @click="medicineDetail(medicine)"
        >
          <div class="left">
            <p class="num f__bold">{{ idx + 1 }}</p>
            <span class="icon"><img :src="medicine.typeImgSrc" /></span>
            <p class="title">{{ medicine.name }}</p>
          </div>
          <div class="right">
            <dl>
              <dt>{{ medicine.categoryName }}</dt>
              <dd>{{ medicine.typeName }}</dd>
            </dl>
          </div>
        </div>
        <!-- // 치료제 -->
      </div>
      <!-- // 치료제 리스트 -->
      <!-- // 치료약 -->
      <div class="popup__main" v-if="popupStep === 2">
        <section class="basic__info__wrap">
          <h5>기본 정보</h5>
          <ul class="vertical__scroll">
            <li>
              <dl>
                <dt>이름</dt>
                <dd>
                  <input
                    type="text"
                    v-model="medicineInfo.name"
                    class="read__only"
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>진료과목</dt>
                <dd>
                  <input
                    type="text"
                    class="read__only"
                    v-model="medicineInfo.subjectName"
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>카테고리</dt>
                <dd>
                  <input
                    type="text"
                    v-model="medicineInfo.categoryName"
                    class="read__only"
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>치료약 타입</dt>
                <dd>
                  <input
                    type="text"
                    v-model="medicineInfo.typeName"
                    class="read__only"
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>레벨</dt>
                <dd>
                  <input
                    type="text"
                    v-model="medicineInfo.level"
                    class="read__only"
                  />
                </dd>
              </dl>
            </li>
            <li
              class="file__input__list"
              v-if="medicineInfo.typeName !== '단어회상'"
            >
              <dl>
                <dt>미디어</dt>
                <dd>
                  <img
                    :src="medicineFileUrl"
                    ref="imageFreeview"
                    v-if="
                      medicineFileType === 'png' ||
                      medicineFileType === 'gif' ||
                      medicineFileType === 'jpg' ||
                      medicineFileType === 'jpeg'
                    "
                  />
                  <video
                    controls
                    v-if="
                      medicineFileType === 'avi' || medicineFileType === 'mp4'
                    "
                    ref="videoFreeview"
                  >
                    <source
                      src="http://ubidtx01.kr.object.ncloudstorage.com/ubi01/1667182874174.mp4"
                      type="video/mp4"
                    />
                  </video>
                  <audio
                    v-if="
                      medicineFileType === 'mpeg' ||
                      medicineFileType === 'wav' ||
                      medicineFileType === 'mp3'
                    "
                    type="audio/mp3"
                    ref="audioFreeview"
                    :src="medicineFileUrl"
                    controls
                  ></audio>
                </dd>
              </dl>
            </li>
          </ul>
        </section>
        <section class="medicine__info__wrap">
          <h5>치료약 정보</h5>
          <SubjectiveTem
            v-if="medicineInfo.typeName === '주관식'"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
          <MultipleChoiceTem
            v-if="medicineInfo.typeName === '객관식'"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
          <ADLTem
            v-if="medicineInfo.typeName === 'ADL'"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
          <OXquizTem
            v-if="medicineInfo.typeName === 'OX퀴즈'"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
          <VideoTem
            v-if="
              medicineInfo.typeName === '동영상' ||
              medicineInfo.typeName === '걷기' ||
              medicineInfo.typeName === '스트레칭'
            "
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
          <WorkSheetTem
            v-if="medicineInfo.typeName === '워크시트'"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
          <AudioTem
            v-if="medicineInfo.typeName === '오디오'"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
          <WordRecallTem
            v-if="medicineInfo.typeName === '단어회상'"
            :updateQuestionInfo="updateQuestionInfo"
            :isPreview="true"
          />
        </section>
      </div>
      <!-- // 치료약 -->
    </div>
  </div>
</template>
<script>
import fileTypeIcon from "@/utill/fileTypeIcon";
import SubjectiveTem from "@/components/MedicineInquiry/MedicineAddPopup/SubjectiveTem";
import MultipleChoiceTem from "@/components/MedicineInquiry/MedicineAddPopup/MultipleChoiceTem";
import ADLTem from "@/components/MedicineInquiry/MedicineAddPopup/ADLTem";
import OXquizTem from "@/components/MedicineInquiry/MedicineAddPopup/OXquizTem";
import VideoTem from "@/components/MedicineInquiry/MedicineAddPopup/VideoTem";
import AudioTem from "@/components/MedicineInquiry/MedicineAddPopup/AudioTem";
import WorkSheetTem from "@/components/MedicineInquiry/MedicineAddPopup/WorkSheetTem";
import WordRecallTem from "@/components/MedicineInquiry/MedicineAddPopup/WordRecallTem";
export default {
  components: {
    WordRecallTem,
    WorkSheetTem,
    AudioTem,
    VideoTem,
    OXquizTem,
    ADLTem,
    MultipleChoiceTem,
    SubjectiveTem,
  },
  data() {
    return {
      medicineList: [],
      popupStep: 1,
      medicineInfo: {},
      medicineFileType: "",
      medicineFileUrl: "",
      updateQuestionInfo: {},
    };
  },
  props: {
    popupEventFlag: Boolean,
    treatmentInfo: Object,
  },
  watch: {
    popupEventFlag() {
      this.medicineList = this.treatmentInfo.digitalMedicine;
      this.medicineList.map((obj) => {
        obj.typeImgSrc = fileTypeIcon(obj.typeName);
      });
    },
  },
  methods: {
    historyBack() {
      this.popupStep = 1;
    },
    popupCloseClick() {
      this.$emit("treatmentPreviewPopClose", "Close");
      this.popupStep = 1;
    },
    medicineDetail(param) {
      this.popupStep = 2;
      this.medicineInfo = param;
      if (this.medicineInfo.questionInfo.length > 0) {
        if (
          Object.prototype.hasOwnProperty.call(
            this.medicineInfo.questionInfo[0],
            "url"
          )
        ) {
          let fileUrl = this.medicineInfo.questionInfo[0].url;
          let fileName = fileUrl.split("/")[fileUrl.split("/").length - 1];
          let fileType = fileName.split(".")[fileName.split(".").length - 1];
          this.medicineFileType = fileType;
          this.medicineFileUrl = fileUrl;
          this.medicineInfo.subjectName = this.treatmentInfo.subjectName;
          this.updateQuestionInfo = this.medicineInfo.questionInfo;
          if (this.$env == "development") console.log(this.medicineInfo);
        }
      }
    },
  },
};
</script>
