<template>
  <div id="printBox" v-if="status === '발행'">
    <h1 class="f__bold">처방전</h1>
    <ul>
      <li>
        <dl>
          <dt class="f__medium">이름&nbsp;&nbsp;:&nbsp;&nbsp;</dt>
          <dd>{{ this.$route.params.name }}</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">나이&nbsp;&nbsp;:&nbsp;&nbsp;</dt>
          <dd>{{ this.$route.params.age }}</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">담당의&nbsp;&nbsp;:&nbsp;&nbsp;</dt>
          <dd>{{ this.$route.params.doctorInCharge }}</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">성별&nbsp;&nbsp;:&nbsp;&nbsp;</dt>
          <dd>{{ this.$route.params.sex }}</dd>
        </dl>
      </li>
    </ul>
    <ul>
      <li>
        <dl>
          <dt class="f__medium">Access code :</dt>
          <dd>{{ patientObj.accessCode }}</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">처방전 비밀번호 :</dt>
          <dd>{{ patientObj.accessPassword }}</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">진료과목 :</dt>
          <dd>{{ patientObj.subjectName }}</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">초진일 :</dt>
          <dd>
            {{
              patientObj.firstTreatDate.replace(
                /(\d{4})(\d{2})(\d{2})/,
                "$1-$2-$3"
              )
            }}
          </dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">병록번호 :</dt>
          <dd>{{ patientObj.patientRegNumber }}</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">키 :</dt>
          <dd>{{ patientObj.height }} cm</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">치료시작일 :</dt>
          <dd>
            {{
              patientObj.startDate !== null
                ? patientObj.startDate.replace(
                    /(\d{4})(\d{2})(\d{2})/,
                    "$1-$2-$3"
                  )
                : ""
            }}
          </dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">몸무게 :</dt>
          <dd>{{ patientObj.weight }} kg</dd>
        </dl>
      </li>
      <li>
        <dl>
          <dt class="f__medium">내복약 :</dt>
          <dd>
            <ul>
              <li
                v-for="(item, idx) in patientObj.medications"
                :key="item.mediId"
              >
                {{ idx + 1 }}. {{ item.mediName }}
              </li>
            </ul>
          </dd>
        </dl>
      </li>
    </ul>
    <h3 class="f__bold">
      QR스캔하여 앱을 설치 해주세요.<br />
      앱 설치 후 Access code와 비밀번호를 입력해서 로그인 하세요<br />
      치료시작일 부터 앱을 통한 치료가 진행됩니다.
    </h3>
    <div class="qr__box">
      <!--  -->
      <div>
        <VueQr :text="AndroidQrUrl" />
        <p>안드로이드 사용자</p>
      </div>
      <!--  -->
      <div>
        <VueQr :text="IosQrUrl" />
        <p>IOS 사용자</p>
      </div>
      <!--  -->
    </div>
  </div>
</template>
<script>
import VueQr from "vue-qr";

export default {
  components: {
    VueQr,
  },
  data() {
    return {
      AndroidQrUrl: "",
      IosQrUrl: "",
      status: "",
      patientObj: {},
    };
  },
  methods: {
    setData(param, appName) {
      this.patientObj = param;
      this.status = "발행";
      if (appName === "알콜중독") {
        this.AndroidQrUrl =
          "https://play.google.com/store/apps/details?id=com.ubiplus.astop";
        this.IosQrUrl = "https://apps.apple.com/us/app/astop/id1615897027";
      } else if (appName === "인지장애") {
        this.AndroidQrUrl =
          "https://play.google.com/store/apps/details?id=com.ubiplus.mindstrong";
        this.IosQrUrl =
          "https://apps.apple.com/us/app/%EB%A7%88%EC%9D%B8%EB%93%9C%EC%8A%A4%ED%8A%B8%EB%A1%B1/id1615895578";
      }
    },
  },
};
</script>
