import { get, create, update, remove } from "@/utill/getData";

export function getPatientList(searchParams) {
  return get(`/patients${searchParams}`);
}

export function addPatient(param) {
  return create("/patients", param);
}

export function updatePatient(param, patientRegNumber) {
  return update(`/patients/${patientRegNumber}`, param);
}

export function removePatient(patientRegNumber) {
  return remove(`/patients/${patientRegNumber}`);
}

export function addPrescription(param, patientRegNumber) {
  return create(`/patients/prescription/${patientRegNumber}`, param);
}

export function updatePrescription(param, patientRegNumber) {
  return update(`/patients/prescription/${patientRegNumber}`, param);
}

export function prescriptionPublish(param, patientRegNumber) {
  return update(`/patients/prescription/publish/${patientRegNumber}`, param);
}

export function getPrescription(param) {
  return get(`/patients/prescription/${param}`);
}

export function getMstrongStatus(patientRegNumber) {
  return get(`/patients/prescription/mStrongStatus/${patientRegNumber}`);
}

export function getAstopStatus(patientRegNumber) {
  return get(`/patients/prescription/aStopStatus/${patientRegNumber}`);
}

export function getPatientDashboard(patientRegNumber) {
  return get(`/patients/prescription/treatmentStatus//${patientRegNumber}`);
}

export function getTreatmentPreview(patientRegNumber, weekNum, dayNum) {
  return get(
    `/patients/prescription/treatmentStatus/${patientRegNumber}/${weekNum}/${dayNum}`
  );
}

export function addPrescriptionTemplate(param) {
  return create(`/patients/prescriptionTemplate`, param);
}
export function updatePrescriptionTemplate(param, pTemplateId) {
  return update(`/patients/prescriptionTemplate/${pTemplateId}`, param);
}
export function getPrescriptionTemplate(subjectId, useYn, name) {
  return get(
    `/patients/prescriptionTemplate?subjectId=${subjectId}&useYn=${useYn}&name=${name}`
  );
}
export function removePrescriptionTemplate(pTemplateId) {
  return remove(`/patients/prescriptionTemplate/${pTemplateId}`);
}

export function resetPrescription(param) {
  return create(
    `/patients/prescription/reset/${param.patientRegNumber}`,
    param
  );
}
